import { mapState } from 'vuex';

export default {
	data() {
		return {
			title: '',
			statusBarHeight: this.statusBarHeight
		};
	},
	computed: {
		...mapState(['role']),
		transRole() {
			return this.role;
		}
	},
	onLoad(value) {
		this.title = '我是 item' + value.id;
	},
	methods: {
		goBack() {
			uni.navigateBack({
				delta: 1
			});
		},
		goHome() {
			uni.switchTab({
				url: '/pages/home/index'
			});
		}
	}
};
